.container {
    background-image: url('../../icons/background.jpg');
    background-size: cover;
    background-repeat: repeat;
    background-attachment: fixed;
    background-position: center;
    min-height: 100vh;
    width: 100%;
    max-width: 800px;
    margin: 0 auto;
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    align-items: center;
    z-index: var(--z-base);
    overflow-x: hidden;
    padding-top: 0;
    margin-top: 0;
}

.naviPanel {
    position: fixed;
    margin-top: 0;
    z-index: var(--z-content);
    height: 2.2rem;
    width: 100%;
    max-width: 800px;
    display: flex;
    background-color: var(--tg-theme-button-color);
    z-index: 200;
}

.nameAndIcon {
    display: flex;
    align-items: center;   
    min-width: 0;          
    flex-grow: 1;          
    width: 100%;  
    gap: 0.5rem; 
    margin-left: 1rem;
    color: var(--tg-theme-button-text-color);
}

.formContainer {
    margin-top: 2.5rem;
    padding: 1rem;
    width: 100%;
    display: flex;
    flex-direction: column;
    gap: 1rem;
}

/* CompanyEditForm.module.css (добавленные/измененные стили) */

.formGroup {
    position: relative;
    width: calc(100% - 2rem); 
    display: flex;
    margin-bottom: 1rem;
    margin-right: 1rem;
    margin-left: 1rem;
}

/* Общие стили для всех полей ввода */
.MuiOutlinedInput-root {
    height: 3rem;
    width: auto !important;
    color: white !important;
    background-color: transparent !important;
}

.MuiOutlinedInput-input {
    padding: 0.75rem 1rem !important;
}

/* Стили для многострочного ввода */
.MuiOutlinedInput-multiline {
    padding: 0.75rem 1rem !important;
    height: auto !important;
}

/* Стили для метки */
.MuiInputLabel-root {
    left: 1rem !important;
    transform-origin: left center !important;
    transform: translateY(-50%) scale(0.9) !important;
}

.MuiInputLabel-shrink {
    transform: translateY(-50%) scale(0.9) !important;
    background-color: rgba(20, 20, 20, 0.8) !important;
    padding: 0 0.3rem !important;
}

/* Стили для выпадающего меню */
.MuiPaper-root {
    background-color: #2d2d2d !important;
    color: white !important;
}

.MuiMenuItem-root {
    color: white !important;
}

.MuiMenuItem-root:hover {
    background-color: rgba(114, 159, 207, 0.2) !important;
}

.MuiCheckbox-root {
    color: white !important;
}

.MuiCheckbox-colorPrimary.Mui-checked {
    color: #729fcf !important;
}

.MuiOutlinedInput-input {
    color: white !important;
    -webkit-text-fill-color: white !important;
}

/* Стили для disabled поля */
.MuiOutlinedInput-root.Mui-disabled .MuiOutlinedInput-notchedOutline  {
    color: white !important;
    -webkit-text-fill-color: white !important;
    border-color: white !important;
    opacity: 0.7;
}

.MuiInputBase-root.Mui-disabled .MuiOutlinedInput-root.Mui-disabled {
    color: white !important;
    -webkit-text-fill-color: white !important;
    opacity: 0.7;
    margin-top: auto;
    margin-bottom: auto;
}

/* Стили для текста в disabled поле */
.MuiOutlinedInput-root.Mui-disabled .MuiOutlinedInput-input {
    color: white !important;
    -webkit-text-fill-color: white !important;
    opacity: 0.7;
}

/* Стили для иконки в Select */
.MuiSelect-icon {
    color: white !important;
}

.MuiSelect-icon.Mui-disabled {
    opacity: 0.7;
}

/* Стиль для выбранного значения в Select */
.MuiSelect-select {
    color: white !important;
    -webkit-text-fill-color: white !important;
}

/* Стиль для выбранного значения в disabled состоянии */
.MuiSelect-select.Mui-disabled {
    opacity: 0.7;
    color: white !important;
    -webkit-text-fill-color: white !important;
}

.emailSection {
  margin-bottom: 20px;
}

.sectionTitle {
  color: white;
  margin-bottom: 15px;
  font-size: 16px;
  font-weight: 500;
}

.companyContactsActivity{
    margin-left: 10px;
}

.checkboxInput {
  appearance: none;
  width: 18px;
  height: 18px;
  border: 2px solid #718096;
  border-radius: 4px;
  align-self: right;
  margin-top: auto;
  margin-bottom: auto;
  margin-right: 12px;
  position: relative;
  cursor: pointer;
  transition: all 0.2s;
  background: transparent;
}

.contactCheckbox {
  appearance: none;
  width: 18px;
  height: 18px;
  border: 1px solid white;
  border-radius: 4px;
  margin-top: auto;
  margin-bottom: auto;
  position: relative;
  cursor: pointer;
  transition: all 0.2s;
  background: transparent;
  float: right;
  vertical-align: middle;
  margin-right: 15px;
}

.contactCheckbox:hover {
  border-color: white;
  /* box-shadow: 0 0 0 1px #4299e1; */
}

.contactCheckbox:checked {
  background: transparent;
  border-color: white;
}

.contactCheckbox:checked::after {
  content: "✓";
  position: absolute;
  color: white;
  font-size: 14px;
  font-weight: bold;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

